import React, { Component } from 'react';
import { Popconfirm, Divider,message, Table,Transfer, Row, Col,Select,Drawer,DatePicker,Icon, Typography, Button,Form,Input,Modal } from 'antd'
import Axios from 'axios'
const FormItem = Form.Item;
const { Option } = Select;
//form代码，没有什么改进，把下面的提交按钮去掉就行
class NormalLoginForm extends Component {
  constructor(props){
    super(props)
  }
   //当点击提交时处理提交数据（未改）
   handleOk = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
    if (!err) {
        console.log(JSON.stringify(values,null,2))
        this.setState({
        AddLoading: true
        });
        Axios({
        method:'post',
        url:'/insertStudentsClass',
        data:{
          ...values
        }
        }
        ).then(res=>{
        if(res.data.data===1){
            this.setState({
            visible: false,
            confirmLoading: false,
            });
            message.success("添加成功")
        }else{
        message.error('添加失败')
      }
        })
    }
    
    });
};
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Drawer
          title="分配学生"
          width={720}
          onClose={this.props.onClose}
          visible={this.props.visable}
        >
          <Form layout="vertical" hideRequiredMark onSubmit={this.handleOk}>
            <Row gutter={16}>
              <Col span={12}>
                <Form.Item label="选择分配的班级">
                  
                  {getFieldDecorator('classid', {
                    rules: [{ required: true, message: '请输入班级' }],
                  })(<Select
                    placeholder="请输入班级"
                    onChange={this.handleSelectChange}
                  >
                    {this.props.data.map(v=>
                      <Option value={v.id}>{v.name}</Option>
                    )}
                  </Select>)}
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={16}>
              <Col span={24}>
                <Form.Item label="学生">
                  {getFieldDecorator('studentid', {
                    rules: [
                      {
                        required: true,
                        message: '请添加学生',
                      },
                    ],
                  })(

                    <Transfer                   
                    dataSource={this.props.mockData}//数据
                    showSearch//是否显示搜索框
                    filterOption={this.props.filterOption}//判读搜索框
                    targetKeys={this.props.targetKeys}//key值
                    onChange={this.props.handleChange}
                    onSearch={this.props.handleSearch}//搜索框内容改变时
                    render={item => item.title}//单行渲染
                    titles={['未添加学生','已添加学生']}
                    listStyle={{
                      width: 250,
                      height: 300,
                    }}
                  />
                  )}
                </Form.Item>
              </Col>
            </Row>
          
          <div
            style={{
              position: 'absolute',
              left: 0,
              bottom: 0,
              width: '100%',
              borderTop: '1px solid #e9e9e9',
              padding: '10px 16px',
              background: '#fff',
              textAlign: 'right',
            }}
          >
            <Button onClick={this.onClose} style={{ marginRight: 8 }}>
              取消
            </Button>
            <Button htmlType="submit" loading={this.props.AddLoading} type="primary">
              提交
            </Button>
          </div>
          </Form>
        </Drawer>
    );
  }
}
 
const Userclassadd = Form.create()(NormalLoginForm);
 
export default Userclassadd;