import React, { Component } from 'react';
import { Form, Icon, Input, Button, Select,Checkbox } from 'antd';
const {Option}=Select
const FormItem = Form.Item;
//form代码，没有什么改进，把下面的提交按钮去掉就行
class NormalLoginForm extends Component {
  constructor(props){
    super(props)
  }
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form className="login-form">
        <FormItem label="老师名">
          {getFieldDecorator('teacherid', {
            rules: [{ required: true, message: '请选择老师名' }],
          })(
            <Select placeholder="搜索">
              {
                this.props.allteacher.map(item=>{
                 return <Option key={item.id}>{item.name}</Option>
                })
              }
            </Select>
          )}
        </FormItem>
        <FormItem label='学院'>
          {getFieldDecorator('collegeid', {
            rules: [{ required: true, message: '请选择学院' }],
          })(
            <Select placeholder="搜索">
              {
                this.props.allCollege.map(item=>{
                  return <Option key={item.id}>{item.name}</Option>
                })
              }
            </Select>
          )}
        </FormItem>
        <FormItem label='班级'>
          {getFieldDecorator('classid', {
            rules: [{ required: true, message: '请选择班级' }],
          })(
            <Select placeholder="搜索">
              {
                this.props.allclass.map(item=>{
                  return <Option key={item.id}>{item.name}</Option>
                })
              }
            </Select>
          )}
        </FormItem>
        <FormItem label='课程'>
          {getFieldDecorator('courseid', {
            rules: [{ required: true, message: '请选择课程' }],
          })(
            <Select placeholder="搜索">
              {
                this.props.allcurse.map(item=>{
                  return  <Option key={item.id}>{item.name}</Option>
                })
              }
            </Select>
          )}
        </FormItem>
      </Form>
    );
  }
}
 
const Userteacheradd = Form.create()(NormalLoginForm);
 
export default Userteacheradd;